"use client";

import React, { useState, useEffect } from "react";
import Link from "next/link";
import { Menu, X } from "lucide-react";
import { cn } from "@/lib/utils";


export default function Navbar() {
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const [scrolled, setScrolled] = useState(false);

  const navLinks = [
    { name: "Home", path: "/" },
    { name: "Services", path: "/services" },
    { name: "Learning Center", path: "/learning" },
    { name: "Gallery", path: "/gallery" },
    { name: "Contact", path: "/contact" },
    { name: "Chat", path: "/chat" }
  ];

  useEffect(() => {
    const handleScroll = () => setScrolled(window.scrollY > 20);
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <header className={cn(
      "fixed top-0 left-0 right-0 z-50 transition-all duration-300",
      scrolled ? "bg-white/80 dark:bg-card/80 backdrop-blur-lg py-3 shadow-md" 
               : "bg-transparent py-5"
    )}>
      <nav className="container flex justify-between items-center">
        <div className="font-bold text-xl">NIS Automation</div>

        <ul className="hidden md:flex space-x-8">
          {navLinks.map(link => (
            <li key={link.name}>
              <Link href={link.path} className="font-medium transition-colors hover:text-primary">
                {link.name}
              </Link>
            </li>
          ))}
        </ul>
      </nav>

      <div className={cn(
        "fixed inset-0 z-40 bg-background/80 backdrop-blur-sm md:hidden transition-opacity duration-300",
        mobileMenuOpen ? "opacity-100" : "opacity-0 pointer-events-none"
      )}>
        <div className={cn(
          "fixed inset-y-0 right-0 w-3/4 max-w-sm bg-card shadow-xl p-6 transition-transform duration-300 ease-in-out",
          mobileMenuOpen ? "translate-x-0" : "translate-x-full"
        )}>
          <div className="flex justify-end mb-8">
            <button onClick={() => setMobileMenuOpen(false)}>
              <X className="h-6 w-6" />
            </button>
          </div>
          <ul className="space-y-6">
            {navLinks.map(link => (
              <li key={link.name}>
                <Link href={link.path} className="text-lg font-medium transition-colors hover:text-primary" onClick={() => setMobileMenuOpen(false)}>
                  {link.name}
                </Link>
              </li>
            ))}
          </ul>
          <Link href="/booking">
            <button className="w-full mt-6 p-2 bg-blue-600 rounded-lg hover:bg-blue-700 text-white" onClick={() => setMobileMenuOpen(false)}>
              Book Service
            </button>
          </Link>
        </div>
      </div>
    </header>
  );
}
